___CSS_LOADER_EXPORT___.push([module.id, "canvas[data-v-5af5b940]{max-height:800px}", "",{"version":3,"sources":["webpack://./resources/assets/js/admin/components/GradesPieChart.vue"],"names":[],"mappings":"AAmEI,wBACI,gBACJ","sourcesContent":["<template>\n    <h2>{{ $gettext('Number of routes per grade') }}</h2>\n    <canvas id=\"radar-chart\"></canvas>\n</template>\n\n<script setup>\n    import { onMounted, computed } from 'vue';\n    import { useStore } from 'vuex';\n    import Chart from 'chart.js/auto';\n    import { randomRgba } from '../../utils/Utils';\n\n    const store = useStore();\n    const stats = computed(() => store.getters.stats);\n\n    onMounted(() => {\n        let canvas = document.getElementById('radar-chart');\n        let chart = Chart.getChart('radar-chart');\n\n        if (chart && typeof chart.destroy === 'function') {\n            chart.destory();\n        }\n\n        chart = new Chart(canvas, {\n            type: 'pie',\n            data: {\n                labels: stats.value.map(item => {\n                    return item.grade;\n                }),\n                datasets: [\n                    {\n                        data: stats.value.map(item => {\n                            return item.total;\n                        }),\n                        backgroundColor: stats.value.map(() => {\n                            return randomRgba();\n                        }),\n                    },\n                ],\n            },\n            options: {\n                scales: {\n                    x: {\n                        stacked: true,\n                    },\n                    y: {\n                        stacked: true,\n                        ticks: {\n                            callback: function (value) {\n                                if (value % 1 === 0) {\n                                    return value;\n                                }\n                            },\n                        },\n                    },\n                },\n                plugins: {\n                    legend: {\n                        position: 'bottom',\n                    },\n                },\n                barThickness: 50,\n            },\n        });\n    });\n</script>\n\n<style scoped>\n    canvas {\n        max-height: 800px;\n    }\n</style>\n"],"sourceRoot":""}]);
